.App {
  max-width: 100vw;
  position: relative;
}
.App-container {
  max-width: 500px;
  width: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 35px 50px rgb(194 195 214 / 50%);
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  letter-spacing: 2px;
  color: white;
}
.Form {
  display: flex;
  position: relative;
  align-items: center;
  padding: 10px;
  background: white;
  margin-top: 20px;
}
.input-check {
  display: inline-block;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  margin-right: 10px;
  background: transparent;
  border: 1px solid #e4e5f1;
}
.checked {
  background: url(./img/icon-check.svg),
    linear-gradient(135deg, #57ddff, #c058f3);
  background-repeat: no-repeat;
  background-position: 50%;
}
.Form-input {
  padding: 10px;
  font-size: 18px;
  margin-left: 10px;
  width: 100%;
  background: transparent;
  outline: none;
  border: 0;
}

/* TodoList component */
.Todolist-wrapper {
  display: flex;
  position: absolute;
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
}

.TodoList {
  display: flex;
  align-items: center;
  padding: 10px;
  background: white;
  border-bottom: 1px solid #e4e5f1;
}
.TodoFooter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  padding: 10px;
  color: #777a92;
  background: white;
  border-bottom: 1px solid #e4e5f1;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.flex span {
  padding: 0 5px;
}
.dragdropmsg {
  margin-top: 5%;
  margin-bottom: 10%;
  text-align: center;
  color: #777a92;
}
.strikethrouw {
  text-decoration: line-through;
}
.delete {
  margin: 5px;
}
